@font-face {
	font-family: 'Minion Pro';
	src: url("./fonts/MinionPro-Regular.woff2") format("woff2"), url("./fonts/MinionPro-Regular.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}

.button.fill.white:hover {
	background-color: #eea70e;
	color: #fff;
	cursor: pointer;
}

html,
body {
	background-color: #ffffff;
	color: #444444;
	font-family: "Open Sans", sans-serif;
	font-size: 16px;
	font-weight: normal;
	margin: 0;
	padding: 0;
	position: relative;
}

html:not(.home):before,
body:not(.home):before {
	background-image: url(../img/uniform-bg-top.png);
	background-repeat: no-repeat;
	background-position: center top;
	background-size: contain;
	content: '';
	/*position: absolute;*/
	top: 0;
	width: 100%;
	height: 250px;
	z-index: 1;
}

p {
	font-size: 18px;
	letter-spacing: 0.60px;
}

.input_title {
	font-family: "Roboto", sans-serif;
	color: #333;
	padding: 0;
	font-weight: 700;
	font-size: 14px;
	margin-bottom: 5px;
}

.slick-slider {
	width: 90vw;
}
.slider-row {
	overflow: hidden;
}

.button {
	display: inline-block;
	font-size: 15px;
	line-height: 37px;
	padding-left: 30px;
	padding-right: 30px;
	-webkit-transition: background-color 500ms ease-in-out;
	-o-transition: background-color 500ms ease-in-out;
	transition: background-color 500ms ease-in-out;
}

.button.disabled {
	background-image: -webkit-linear-gradient(135deg, #1128a4, #21535f, #3f2381);
	background-image: -o-linear-gradient(135deg, #1128a4, #21535f, #3f2381);
	background-image: linear-gradient(-45deg, #1128a4, #21535f, #3f2381);
}

.button.medium {
	font-size: 18px;
	line-height: 52px;
	padding-left: 40px;
	padding-right: 40px;
}

.button.fill.accent {
	background-color: #ffca15;
	border-color: #ffca15;
	border-style: solid;
	border-width: 1px;
	color: #121212;
}

.button.fill.accent:hover {
	cursor: pointer;
	background-color: #eea70e;
	color: #ffffff;
}

.button.fill.white {
	background-color: #ffffff;
	color: #444444;
}

.button.outline {
	border-style: solid;
	border-width: 1px;
	color: #ffffff;
}

.button.outline.accent {
	border-color: #ffca15;
	color: #ffca15;
}

.button.outline.accent:hover {
	cursor: pointer;
	border-color: #eea70e;
	color: #ffffff;
}

.button.outline.white {
	border-color: #ffffff;
	color: #ffffff;
}

.button.outline.white:hover {
	cursor: pointer;
	border-color: #eea70e;
	color: #eea70e;
}

.button.round {
	border-radius: 4px;
}

.button.circle {
	border-radius: 30px;
}

.button:hover {
	text-decoration: none;
}

.button:focus {
	outline: none;
}

.divider {
	height: 1px;
	width: 100%;
	background-color: rgba(255, 255, 255, 0.12);
	margin-bottom: 30px;
	margin-top: 30px;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type="number"] {
	-moz-appearance: textfield;
}

.top-bar {
	background-color: #2a6b72;
	display: none;
	padding: 0.5rem 0;
}

.select_line {
	display: -ms-flexbox;
	display: flex;
}

.date-select-wrap {
	width: 26%;
}

.navbar {
	background-color: #2a6b72;
	position: relative;
}

.navbar .container {
	position: relative;
	z-index: 2;
}

.navbar .navbar-toggler {
	background-color: #eea70e;
	border-color: #eea70e;
}

.navbar .navbar-button {
	margin: 0 7.5px;
}

.navbar .nav {
	-ms-flex-order: 2;
	    order: 2;
}

.navbar .nav .nav-item {
	padding-left: 20px;
	padding-right: 20px;
}

.navbar .nav .nav-item .nav-link {
	color: #ffffff;
	display: block;
	font-size: 15px;
	padding-left: 0;
	padding-right: 0;
	letter-spacing: 0.60px;
	position: relative;
}

.navbar .nav .nav-item .nav-link:after {
	content: '';
	width: 100%;
	height: 1px;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #ffca15;
	-webkit-transition: width 500ms ease-in-out;
	-o-transition: width 500ms ease-in-out;
	transition: width 500ms ease-in-out;
	margin: 0 auto;
	width: 0;
}

.navbar .nav .nav-item .nav-link:active:after,
.navbar .nav .nav-item .nav-link.active:after,
.navbar .nav .nav-item .nav-link:hover:after {
	width: 100%;
}

