}

.faq-list {
	position: relative;
}

.faq-list:before {
	background-image: url(../img/line-bg.png);
	background-repeat: no-repeat;
	content: '';
	width: 7px;
	height: 100%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	top: 0;
	bottom: 0;
	position: absolute;
	background-position: center;
}

.faq-list .faq-list__inner {
	margin: 0;
	padding: 15px;
	border: 1px solid #fff;
}

.faq-list .faq-list__inner li {
	list-style: none;
	margin-bottom: 1px;
}

.faq-list .faq-list__inner li .faq_main {
	border: 1px solid rgba(42, 107, 114, 0.29);
	color: #1c5c63;
	padding: 10px 15px;
	font-family: "Roboto", sans-serif;
	font-size: 18px;
	letter-spacing: 0.60px;
	cursor: pointer;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 10px;
	position: relative;
}

.faq-list .faq-list__inner li .faq_main:after {
	content: '';
	background-color: #81a9ad;
	width: 14px;
	height: 2px;
	right: 15px;
	top: 50%;
	position: absolute;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.faq-list .faq-list__inner li .faq_main:before {
	content: '';
	background-color: #81a9ad;
	width: 2px;
	height: 14px;
